.article {
  padding: 30px 0;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  border-bottom: 1px solid rgba($bg-color, .3);
  &:last-of-type {
    border-bottom: 0;
    margin-bottom: 40px; }
  & > h1 {
    font-size: 1.8em;
    margin: 0;
    a {
      text-decoration: none;
      color: $bg-color; } }
  & > .date {
    min-width: 170px;
    font-weight: 300;
    color: rgba($bg-color, .8);
    text-align: right; } }
