.header {
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  overflow: hidden;
  &:after {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    background: linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.42) 40%, rgba(0,0,0,0) 100%);
    content: '';
    z-index: 1; } }

.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
  @extend %transition;
  &.scroll {
    background-color: $bg-color; } }

.logo {
  padding: 15px 20px;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  letter-spacing: 2px;
  text-decoration: none;
  color: $white; }

.site-nav {
  & > ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: inline-flex; }
  a {
    padding: 15px 20px;
    font-weight: 300;
    text-decoration: none;
    letter-spacing: 1.2px;
    color: $white; } }
